@mixin media-visibility-utilities($prefix: "") {
	.#{$prefix}#{class("visible")} { visibility: visible !important; }
	.#{$prefix}#{class("hidden")} { visibility: hidden !important; }
}

@mixin media-order-utilities($prefix: "") {
	.#{$prefix}#{class("item-first")} { order: -1 !important; }
	.#{$prefix}#{class("item-last")} { order: 1 !important; }
}

@mixin media-float-utilities($prefix: "") {
    .#{$prefix}#{class("floatbox")}::after {
    	content: "" !important;
    	display: block !important;
    	clear: both !important;
    }

    .#{$prefix}#{class("float-left")} { float: left !important; }
    img.#{$prefix}#{class("float-left")} { margin-right: spacer($margin-base) !important; }

    .#{$prefix}#{class("float-right")} { float: right !important; }
    img.#{$prefix}#{class("float-right")} { margin-left: spacer($margin-base) !important; }

    img.#{$prefix}#{class("float-left")},
    img.#{$prefix}#{class("float-right")} {
    	margin-bottom: spacer($margin-base) !important;
    }

    .#{$prefix} {
        .#{class("clear")} { clear: both !important; }
        .#{class("clear-left")} { clear: left !important; }
        .#{class("clear-right")} { clear: right !important; }

        // https://developer.mozilla.org/fr/docs/Web/CSS/Block_formatting_context
        .#{class("block-formatting-context")} { overflow: hidden !important; }
    }
}

@mixin media-utilities($area: "default", $prefix: "") {
    @include utility-option("text", $area) {
        @include media-text-utilities($prefix);
    }
    @include utility-option("border", $area) {
        @include media-border-utilities($prefix);
    }
    @include utility-option("color", $area) {
        @include media-color-utilities($prefix);
    }
    @include utility-option("size", $area) {
        @include media-size-utilities($prefix);
    }
    @include utility-option("display", $area) {
        @include media-display-utilities($prefix);
    }
    @include utility-option("align", $area) {
        @include media-align-utilities($prefix);
    }
    @include utility-option("margin", $area) {
        @include media-margin-utilities($prefix);
    }
    @include utility-option("padding", $area) {
        @include media-padding-utilities($prefix);
    }
    @include utility-option("visibility", $area) {
        @include media-visibility-utilities($prefix);
    }
    @include utility-option("order", $area) {
        @include media-order-utilities($prefix);
    }
    @include utility-option("float", $area) {
        @include media-float-utilities($prefix);
    }
}

@include option("utilities") {
    .#{class("visually-hidden")} {
    	position: absolute !important;
    	border: 0 !important;
    	height: 1px !important;
    	width: 1px !important;
    	padding: 0 !important;
    	overflow: hidden !important;
    	clip: rect(0, 0, 0, 0) !important;
    }

    @include media-utilities();
}
