:root {
	--spacer-base: 1rem;
	--body-bg: #fff;
	--body-color: #0d0d0d;
	--body-bg-inverted: #262626;
	--body-color-inverted: #f2f2f2;
	--link-color: #3935cd;
	--link-hover-focus-color: #272491;
	--link-visited-color: #8345c7;
	--link-visited-hover-focus-color: #5d2c94;
	--link-color-inverted: #74c0cd;
	--link-hover-focus-color-inverted: #41a3b4;
	--link-visited-color-inverted: #aa80d9;
	--link-visited-hover-focus-color-inverted: #8345c7;
}

@media (prefers-color-scheme: dark) {
	:root {
		--body-bg: #262626;
		--body-color: #f2f2f2;
		--body-bg-inverted: #fff;
		--body-color-inverted: #0d0d0d;
		--link-color: #74c0cd;
		--link-hover-focus-color: #41a3b4;
		--link-visited-color: #aa80d9;
		--link-visited-hover-focus-color: #8345c7;
		--link-color-inverted: #3935cd;
		--link-hover-focus-color-inverted: #272491;
		--link-visited-color-inverted: #8345c7;
		--link-visited-hover-focus-color-inverted: #5d2c94;
	}
}

@media (prefers-reduced-motion: reduce) {
	* {
		transition: none !important;
		animation: none !important;
	}
}

*,
*::before,
*::after {
	box-sizing: border-box;
	max-width: 100%;
}

html {
	font-family: sans-serif;
	font-size: 62.5%;
	font-size: calc(1em * .625);
	line-height: 1.15;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
	display: block;
}

@media (max-width: 575px) {
	div,
    textarea,
    table,
    td,
    th,
    code,
    pre,
    samp {
		word-wrap: break-word;
		hyphens: auto;
		overflow-wrap: break-word;
	}
}

body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
	font-size: 1.4rem;
	font-weight: 400;
	line-height: 1.5;
	margin: 0;
	text-align: left;
	color: var(--body-color);
	background: var(--body-bg);
	scroll-behavior: smooth;
	-webkit-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media (min-width: 576px) {
	body {
		font-size: 1.6rem;
	}
}

@media (prefers-reduced-motion: reduce) {
	body {
		scroll-behavior: auto;
	}
}

